body, html {
    margin: 0;
    font: {
      family: $font-family;
      size: $font-size;
      weight: 400;
    }
    background: $bg-color;
    color: $text-color;
  }

  body {
    min-width: 320px;
    height: auto;
    min-height: 100%;
    overflow-x: hidden;
  }

  a {
    color: $text-color;
    transition: all .4s ease;
    &, &:hover, &:focus {
      outline: 0;
      text-decoration: none;
    }
  }

  main {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100vh;
  }

  .title {
    font-size: 20px;
    font-weight: bold;
  }

  .button {
    border: none;
    border-radius: 3px;
    background-color: rgb(40, 110, 250);
    box-shadow: 0px 10px 20px 0px rgba(78, 132, 253, 0.2);
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    padding: 15px 25px;
  }

  